body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  background: #e5e5e5;
}

a {
  color: #FFFFFF;
}

button {
  transition: all .2s ease-out;
}

.hidden {
  display: none;
}

.loader {
  background: #3E3C41;
  height: 100vh;
  opacity: 0.5;
  position: relative;
}

.loader_image {
  display: block;
  margin: auto;
  background-image: url(/train/static/media/loading.6c4f0b1d.gif);

  width: 550px;
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}



/*~~~~~~~ HEADER & NAV MENU ~~~~~~~~*/
.header {
  position: absolute;
  width: 100vw;
  z-index: 100;
  line-height: normal;
  background: rgba(0, 0, 0, 0.6);
}

.menu-burger {
  text-align: right;
  color: #fff;
  height: 50px;
  font-size: 36px;
  line-height: 50px; 
  margin: 0 15px;
  padding: 5px;
  cursor: pointer;
}
.menu-burger i:hover {
  color: #FFA800;
  transition: ease-in-out 0.7s;

}
.menu-dropdown {
  height: auto;
  width: 100vw;
  display: none;
}
.menu-dropdown .nav {
  padding: 0;
  height: auto;
}
.menu-dropdown .nav__link {
line-height: 40px;
font-size: 20px;
}
.menu-dropdown .nav__item {
  display: block;
}

.logo, .nav {
  margin: 0px 30px;
}
.logo__link {
  text-decoration: none;
}

.logo__link h1 {
margin-top: 0;
margin-bottom: 0;
padding: 15px 0px;
font-size: 45px
}


nav, .nav {
  background: #292929;
  box-sizing: border-box;
  height: 75px;
  list-style: none;
  padding-left: 0;
}

.nav__item {
 display: inline-block;
 margin-right: 100px;
}

.nav__link {
  font-weight: 100;
  font-size: 30px;
  text-decoration: none;
  line-height: 75px;
}
.nav__link:hover {
  color: #FFCA62;
  transition: ease-in-out 0.5s;
}


/*~~~~~~~ MAIN SECTION ~~~~~~~*/

.motto {
  font-weight: 300;
  text-shadow: 4px 4px 6px rgba(0, 0, 0, 1);
  font-size: 54px;
  line-height: normal;
  color: #FFFFFF;
  padding-bottom: 150px;
}

.motto-bold {
  font-weight: 700; 
}

.main__picker, .main__motto {
  margin: 0 auto;
}

.main {
  width: 100%;
  display: -webkit-flex;
  display: flex;
-webkit-justify-content: center;
        justify-content: center;
-webkit-align-items: flex-end;
        align-items: flex-end;
  position: relative;
  background-image: url(/train/static/media/main-1.335de249.jpg);

  background-repeat: no-repeat;
  background-size: cover;
  min-height: 700px;
  background-position: center;
}

.main::after {
  content: "";
  width: 100%;
  height: 8px;
  display: block;
  position: absolute;
  bottom: -8px;
  background-color:#FFA800;
}


