/* ~~~~~~~ PROGRESS STEPS ~~~~~~~ */
.progress {
  background: #3E3C41;
  color: #FFFFFF;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  position: relative;
}
.number {
  display: inline-block;
  border: 2px solid #FFFFFF;
  border-radius: 50%;
  font-size: 26px;
  width: 30px;
  height: 30px;
  text-align: center;
