}
.progress__text {
  z-index: 5;
}
.progress__step {
  font-size: 20px;
  position: relative;
  box-sizing: border-box;
  z-index: 0;
  -webkit-flex: 1 1;
          flex: 1 1;
  text-align: center;
}

.progress__arrow {
  position: absolute;
  height: 100%;
  left: 100%;
  top: 0;
  transition: linear  1.3s;
}
.progress__arrow::before,
.progress__arrow::after {
  content: "";
  display: block;
  position: relative;
  left: -6px;
  z-index: 2;
  background-color: transparent;
  height: 55%;
  padding-left: 15px;
  z-index: 10;
  border-right: 1px solid #fff;
}
.progress__arrow::before {
  -webkit-transform: rotate(-25deg);
          transform: rotate(-25deg);
  top: 1.5px;
  z-index: 1;
}
.progress__arrow::after {
  -webkit-transform: rotate(25deg);
          transform: rotate(25deg);
  top: -9px;
  z-index: 2;
}

.progress__step-current,
.progress__arrow-current::before,
.progress__arrow-current::after {
  background-color: #FFA800;
}



