/* Typeahead Component */

.typeahead-input{
  margin-bottom: 2px; /* для маленькой черной полосочки */
}
.typeahead-selectable {
  border-radius: 3px;
  background-color: #fff;
  height: 30px;
  color: #292929;
  cursor: pointer;
  transition: all .2s ease-out;
  line-height: 2; /* чтобы сработал vertical-align */
  vertical-align: middle;
}
.typeahead-selectable:hover{
  background-color: rgb(222, 218, 218);
}
.typeahead-hint { /* для подставляемого светло-серого текста в инпуте */
  color: #c4c4c4; 

}

.tt-menu.typeahead-open {
